import React, { useState, useEffect } from 'react'
import './board.styles.scss'
import Square from '../square/square.component'
import { Patterns } from '../../patterns'

function Board() {
    const [board, setBoard] = useState(Array(9).fill(''))
    const [player, setPlayer] = useState('X')
    const [result, setResult] = useState({ winner: 'none', state: 'none' })

    useEffect(() => {
        checkWin()
        checkIfTie()
        player == 'O' ? setPlayer('X') : setPlayer('O')
    }, [board])

    useEffect(() => {
        if (result.state != 'none') {
            alert(`Игра окончена ! Победил игрок :${result.winner}`)
        }

        restartGame()

    }, [result])

    const playerTurn = (square) => {
        setBoard(board.map((value, index) => {
            if (index == square && value == '') {
                return player
            }
            return value
        }))
    }

    const checkWin = () => {
        Patterns.forEach((currentPattern) => {
            const firstPlayer = board[currentPattern[0]]
            if (firstPlayer == '') return
            let foundWinningPattern = true
            currentPattern.forEach((index) => {
                if (board[index] != firstPlayer) {
                    foundWinningPattern = false
                }
            })
            if (foundWinningPattern) {
                setResult({ winner: player, state: 'Победа' })
            }
        })
    }

    const checkIfTie = () => {
        let filled = true
        board.forEach((square) => {
            if (square == "") {
                filled = false
            }
        })

        if (filled) {
            setResult({ winner: 'Ничья', state: 'Ничья' })
        }
    }

    const restartGame = () => {
        setBoard(Array(9).fill(''))
        setPlayer('O')
    }

    return (
        <div className='board'>
            <div className='row'>
                <Square value={board[0]} playerTurn={() => { playerTurn(0) }} />
                <Square value={board[1]} playerTurn={() => { playerTurn(1) }} />
                <Square value={board[2]} playerTurn={() => { playerTurn(2) }} />
            </div>
            <div className='row'>
                <Square value={board[3]} playerTurn={() => { playerTurn(3) }} />
                <Square value={board[4]} playerTurn={() => { playerTurn(4) }} />
                <Square value={board[5]} playerTurn={() => { playerTurn(5) }} />
            </div>
            <div className='row'>
                <Square value={board[6]} playerTurn={() => { playerTurn(6) }} />
                <Square value={board[7]} playerTurn={() => { playerTurn(7) }} />
                <Square value={board[8]} playerTurn={() => { playerTurn(8) }} />
            </div>
        </div>
    )
}

export default Board